import img from 'assets/images/question.svg';
import { IconValue } from 'components/Assets/Icon';
import { BtnType, Button } from 'components/Button';
import * as React from 'react';
import { FormattedMessage } from 'react-intl';
import Dialog, { IDialogProps, IDialogState } from './Dialog';
const Img: any = img;

/**
 * @props
 */
interface IProps extends IDialogProps {
  onRemove: () => void;
}
interface IState extends IDialogState {}

class DialogMandateRemove extends React.PureComponent<IProps, IState> {
  static defaultProps = {
    className: 'alert',
    closable: true,
    description: 'dialog.mandate.description',
    footerIcon: null,
    footerTitle: 'dialog.mandate.link',
    icon: IconValue.Alert,
    img: <Img />,
    title: 'dialog.mandate.title',
  };

  render() {
    const { className, ...rest } = this.props;
    return (
      <Dialog className={`onboarding-dialog ${className}`} {...rest}>
        <Button type={BtnType.Primary} onClick={this.props.onRemove}>
          <FormattedMessage id="dialog.mandate.btn_remove" />
        </Button>
      </Dialog>
    );
  }
}

export default DialogMandateRemove;
